import { ActionTypes } from '../actions/todo';
// initialze state
const initialState = {
    todos: []
};
export const rootReducer = (state = initialState, action) => {
    switch (action.type) {
        case ActionTypes.ADD: {
            return Object.assign({}, state, { todos: [...state.todos, action.payload.todo] });
        }
        case ActionTypes.TOGGLE: {
            const toggleId = action.payload.id;
            return Object.assign({}, state, { todos: state.todos.map(todo => todo.id === toggleId ? Object.assign({}, todo, { status: !todo.status }) : todo) });
        }
        case ActionTypes.DELETE_DONE: {
            return Object.assign({}, state, { todos: state.todos.filter(todo => !todo.status) });
        }
        default:
            return state;
    }
};
